/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2013-01-11-oahu-day-1/index.md absPath of file >>> MarkdownRemark","html":"<p>It snowed 4 inches today in Logan and the wind is bad enough that the Salt Lake Airport is closed. Meanwhile, it's mid 70s on Oahu. Hate me. I'm going to hate me when we have to go back. I'm not sure why I don't live here.</p>\n<p>We visited the Dole Plantation. You can purchase Dole Pineapple Whip at Disneyland and even in Ogden, but it tastes better at the plantation. Not really, but telling yourself that makes you feel better what you pay for it. Somehow I couldn't feel better about buying a pineapple the cost of which increases exponentially the closer to the field you get. Not sure how shipping it to Utah makes it so much cheaper than if you want to buy it a few feet from the field.</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-cc900.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 5955\" title=\"\" src=\"/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-b80fa.jpg\" srcset=\"/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-cf410.jpg 163w,\n/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-62f2a.jpg 325w,\n/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-b80fa.jpg 650w,\n/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-08cb4.jpg 975w,\n/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-f2bf9.jpg 1300w,\n/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-fd4ba.jpg 1950w,\n/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-cc900.jpg 3240w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>You can buy pearls in the oyster for really cheap and for your 'convenience' they have a bunch of settings for you to put it in</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-ddadf.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 5947\" title=\"\" src=\"/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-b80fa.jpg\" srcset=\"/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-cf410.jpg 163w,\n/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-62f2a.jpg 325w,\n/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-b80fa.jpg 650w,\n/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-08cb4.jpg 975w,\n/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-f2bf9.jpg 1300w,\n/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-fd4ba.jpg 1950w,\n/static/IMG_5947-343259b6cdd19d823e08c5bc088c14a5-ddadf.jpg 4320w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>Next we visited a nude beach. Not really, but we looked positively dressed. Dental floss doesn't count as a swimming suite in Utah, but at Waimea beach on the North Shore it's a standard uniform. We played in the water. I still have salt boogers that I might have to live with for the rest of my life. Waimea is spectacular and one of my favorite beaches from the trip:</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_6018-0629cfc8fc60cabe106660428090f467-ddadf.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 6018\" title=\"\" src=\"/static/IMG_6018-0629cfc8fc60cabe106660428090f467-b80fa.jpg\" srcset=\"/static/IMG_6018-0629cfc8fc60cabe106660428090f467-cf410.jpg 163w,\n/static/IMG_6018-0629cfc8fc60cabe106660428090f467-62f2a.jpg 325w,\n/static/IMG_6018-0629cfc8fc60cabe106660428090f467-b80fa.jpg 650w,\n/static/IMG_6018-0629cfc8fc60cabe106660428090f467-08cb4.jpg 975w,\n/static/IMG_6018-0629cfc8fc60cabe106660428090f467-f2bf9.jpg 1300w,\n/static/IMG_6018-0629cfc8fc60cabe106660428090f467-fd4ba.jpg 1950w,\n/static/IMG_6018-0629cfc8fc60cabe106660428090f467-ddadf.jpg 4320w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n \n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_5973-842298402819c524982a25c48c1a1feb-ddadf.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 5973\" title=\"\" src=\"/static/IMG_5973-842298402819c524982a25c48c1a1feb-b80fa.jpg\" srcset=\"/static/IMG_5973-842298402819c524982a25c48c1a1feb-cf410.jpg 163w,\n/static/IMG_5973-842298402819c524982a25c48c1a1feb-62f2a.jpg 325w,\n/static/IMG_5973-842298402819c524982a25c48c1a1feb-b80fa.jpg 650w,\n/static/IMG_5973-842298402819c524982a25c48c1a1feb-08cb4.jpg 975w,\n/static/IMG_5973-842298402819c524982a25c48c1a1feb-f2bf9.jpg 1300w,\n/static/IMG_5973-842298402819c524982a25c48c1a1feb-fd4ba.jpg 1950w,\n/static/IMG_5973-842298402819c524982a25c48c1a1feb-ddadf.jpg 4320w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n \n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-ddadf.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 6006\" title=\"\" src=\"/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-b80fa.jpg\" srcset=\"/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-cf410.jpg 163w,\n/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-62f2a.jpg 325w,\n/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-b80fa.jpg 650w,\n/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-08cb4.jpg 975w,\n/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-f2bf9.jpg 1300w,\n/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-fd4ba.jpg 1950w,\n/static/IMG_6006-417f5c60a5c74d7cab4bb9290fa9eeff-ddadf.jpg 4320w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>Dinner was from a truck. Generally I try to avoid purchasing food from establishments that can escape the health department with the turn of a key. Giovanni's shrimp truck is famous. That makes it 'safe'. I'll let you know tomorrow how that worked out. There was a smoothie truck next door. That was one of the better Piña Coladas I've had.</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-db0af.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 1470\" title=\"\" src=\"/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-b80fa.jpg\" srcset=\"/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-cf410.jpg 163w,\n/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-62f2a.jpg 325w,\n/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-b80fa.jpg 650w,\n/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-08cb4.jpg 975w,\n/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-f2bf9.jpg 1300w,\n/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-fd4ba.jpg 1950w,\n/static/IMG_1470-d1a9d322bf374de1c7fc2b09c4dcb71b-db0af.jpg 3264w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>The capstone of any day should be Chocolate. When spending time in an extraordinary place you need an extraordinary chocolate. <a href=\"http://madrechocolate.com/Home.html\">Madre Chocolate</a> provided chocolate tasting and background. Nat, the flavormiester, walked us from raw bean to finished product. The flavor of raw, unprocessed beans isn't as enjoyable as the finished product, but it's fun to try chocolate at each point in the process. If you want a uniquely Hawaiian chocolate you need to try the <a href=\"http://madrechocolate.com/Shop.html\">Hawaiian Passion Fruit 70% Dark Chocolate or the Coconut Milk &#x26; Caramelized Ginger.</a></p>\n<p>Nathan the Flavormeister. I'd be pretty happy too if my job was making chocolate. I think I'd probably weight 800 pounds though.</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-db0af.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 1478\" title=\"\" src=\"/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-b80fa.jpg\" srcset=\"/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-cf410.jpg 163w,\n/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-62f2a.jpg 325w,\n/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-b80fa.jpg 650w,\n/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-08cb4.jpg 975w,\n/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-f2bf9.jpg 1300w,\n/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-fd4ba.jpg 1950w,\n/static/IMG_1478-1c81d5bab198f3e034ebbc3fbf8a8e6d-db0af.jpg 3264w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>A cacao plant growing outside of Madre Chocolate:</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-cc900.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 133.33333333333331%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 6035\" title=\"\" src=\"/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-b80fa.jpg\" srcset=\"/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-cf410.jpg 163w,\n/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-62f2a.jpg 325w,\n/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-b80fa.jpg 650w,\n/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-08cb4.jpg 975w,\n/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-f2bf9.jpg 1300w,\n/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-fd4ba.jpg 1950w,\n/static/IMG_6035-b091e0f6cdd875676a01d10b1da824de-cc900.jpg 3240w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>Kristen is a big fan of dark chocolate:</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-db0af.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"IMG 1471\" title=\"\" src=\"/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-b80fa.jpg\" srcset=\"/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-cf410.jpg 163w,\n/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-62f2a.jpg 325w,\n/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-b80fa.jpg 650w,\n/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-08cb4.jpg 975w,\n/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-f2bf9.jpg 1300w,\n/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-fd4ba.jpg 1950w,\n/static/IMG_1471-8c4ae492e1d900b28d754528e1e0b5c3-db0af.jpg 3264w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>I ended up blowing a wad of cash here. I couldn't resist. The flavors are unique and amazing.</p>","frontmatter":{"title":"Oahu Day 1","date":"January 11, 2013","imageUrl":null,"image":{"childImageSharp":{"sizes":{"src":"/static/IMG_5955-6470f1b6e35af3bb0f43f3b78f085638-381d6.jpg"}}}}}},"pathContext":{"path":"/oahu-day-1"}}

/***/ })

